* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.App {
  min-height: 100vh;
  background-color:#fff;

}
.h4style{
  color:rgb(241, 153, 102);

}
.image-container {
  position: relative;
  height: auto;
}
.spanTitle {
  color: rgb(248, 139, 76);
}
.logo {
  position: absolute;
  top: 47px;
  left: 6%;
  color: rgb(241, 153, 102);
}
.logoStyle {
  position: absolute;
  height: 5%;
  top: 10px;
  left: 4%;
}

.titlehome {
  position: absolute;
  top: 28%;
  left: 6%;
  color: rgb(255, 255, 255);
}
.h1-title {
  font-size: 50px;
}

.form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}
.input {
  width: 50%;
  border: none;
  padding: 12px;
  border: 1px solid #ccc;
  margin-right: 6px;
}
.search {
  border: none;
  background: rgb(0, 59, 87);
  padding: 12px 20px;
  color: white;
}
.search:hover {
  color: rgb(110, 180, 212);
}
